import React from "react";
import Layout from "./Layout";

const styles = {
  background: "black",
  height: "1000px",
  color: "white",

  textAlign: "center"
};

const styleSheet = {
  container: styles,
  contents: {
    marginTop: "20px",
    fontSize: "5em"
  }
};
const View = () => {
  return (
    <Layout title="View">
      <div style={styleSheet.styles}>
        <div style={styleSheet.contents}>View</div>
      </div>
    </Layout>
  );
};

export default View;
